.App {
  background-image: url(./cubes_@2X.png);
  display: grid;
  grid-auto-rows: auto 1fr auto;
  grid-template-columns: auto minmax(80vw, 400px) auto;
  min-height: 100vh;
}
footer, header {
  background-image: url(./diagmonds.png);
}
footer *, header * {
  color: #ffffff;
}
footer {
  grid-column: 1/4;
  grid-row: 3;
  text-align: center;
}
header {
  grid-column: 1/4;
  grid-row: 1;
}
header h1 {
  margin: 0.83em;
}
main {
  grid-column: 2;
  grid-row: 2;
}
main div {
  background-color: #f0f0f0;
  margin: 1em 0;
  padding: 1em;
  text-align: justify;
}
main div#contact {
  overflow: hidden;
}
table {
  line-height: 2em;
}
table a {
  margin: 0.25em;
  padding: 0.25em;
}
table th {
  text-align: right;
}
